div {
    -webkit-touch-callout: none;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}


.detail-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    /* ë˜ëŠ” centerë¡œ ë°”ê¾¸ë©´ ì™„ì „ ì„¸ë¡œ ê°€ìš´ë° */
    padding-top: 6vh;
    padding-bottom: 3vh;
    background-color: #000;
    min-height: 100vh;
    color: #fff;
}

.team-title-image {
    max-width: 300px;
    height: auto;
    margin-bottom: 2rem;
    display: block;
}

/* íƒ€ì´í‹€ ìŠ¤íƒ€ì¼ */
.detail-title {
    font-size: 2rem;
    text-align: center;
    color: #e06363;
    font-family: 'Gowun Dodum', sans-serif;
    margin-bottom: 2rem;
}

/* íŒ€ ìºë¦­í„° ëª©ë¡ */
.team-scroll {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    width: 100%;
    max-width: 1200px;
    padding: 0 1rem;
}

/* ê°œë³„ ì¹´ë“œ ìŠ¤íƒ€ì¼ */
.person-card {
    width: 200px;
    height: 280px;
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid #e06363;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;
}

.person-card:hover {
    transform: scale(1.03);
}

/* ì´ë¯¸ì§€ê°€ ë°•ìŠ¤ì— ê½‰ ì°¨ê³ , ì¤‘ì•™ ë§ì¶¤ */
.person-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* ë°•ìŠ¤ ì±„ìš°ê¸° */
    object-position: center;
    /* ì–¼êµ´ ì¤‘ì•™ ìœ ì§€ */
    display: block;
}

.back-button-wrapper {
    display: flex;
    justify-content: center;
    margin: 2rem 0;
}

.back-button {
    background: linear-gradient(to top, #222, #444);
    border: 2px solid #666;
    color: #eee;
    font-family: 'IM Fell English SC', serif;
    padding: 12px 24px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1.2rem;
    text-shadow: 0 0 6px #ff0000;
    transition: all 0.3s ease;
}

.back-button:hover {
    background: linear-gradient(to top, #444, #666);
    transform: translateY(-4px);
    box-shadow: 0 0 12px #aa0000;
}